import React from 'react';
import logo from '../img/logo.png';

export function Navbar() {
  return (
    <div className='navBar'>
      <img alt={'logo'} className={'navLogo'} src={logo}></img>
      <div className={'aboutBtn'}>
        <center>
          <b>About</b>
        </center>
      </div>
    </div>
  );
}
